.footer {
  display: flex;
  align-items: center;
  min-height: 108px;

  background-color: $color-black-russian;
}

.footer__wrapper {
  display: flex;
  justify-content: space-between;
  max-width: 1366px;
  padding-top: 44px;
  padding-bottom: 26px;

  gap: 100px;

  @include vp-1199 {
    position: relative;
    justify-content: start;
    max-width: 100%;
    padding-top: 33px;
    gap: 0;
  }

  @include vp-767 {
    flex-wrap: wrap;
  }

  @media (max-width: 320px) {
    gap: 11px;
    padding-top: 43px;
  }
}

.footer__logo {
  @include vp-1199 {
    position: absolute;
    top: 36px;
    left: 0;
    margin: 0;
  }

  @include vp-767 {
    top: 45px;
  }

  a {
    margin: 0;

    svg {
      @include vp-1199 {
        width: 82px;
        height: 17px;

        @include vp-767 {
          width: 69px;
          height: 24px;
        }
      }
    }
  }
}

.footer__navigation {
  ul {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 0 0 0 5px;
    padding: 0;

    list-style: none;

    gap: 20px;

    @include vp-1199 {
      margin: 0;
      margin-left: 100px;
      column-count: 2;
      gap: 31px;
    }

    @include vp-767 {
      display: block;
      margin: 0;
      margin-bottom: 16px;
      column-count: 2;
    }
  }

  li {
    @include vp-767 {
      margin-top: 16px;

      &:first-of-type {
        margin-top: 45px;
      }
    }
  }

  a {
    padding: 10px;

    font-size: 14px;
    font-weight: 400;
    line-height: 24px;

    color: $color-default-white;

    @include vp-1199 {
      padding: 0;

      line-height: 19px;

      &:focus-visible {
        padding: 10px;
      }
    }

    @include vp-767 {
      font-size: 16px;
    }

    @include hover {
      text-decoration: underline;
      text-underline-offset: 10px;

      &:active {
        text-decoration: none;
      }
    }

    &:focus-visible {
      border: 1px solid $color-default-white;
      outline: none;
    }
  }

  @include vp-1199 {
    display: block;
    width: 100%;
  }
}

.footer__socials {
  ul {
    display: flex;
    flex-wrap: wrap;
    max-width: 100px;
    margin: 0;
    padding: 0;

    list-style-type: none;
    line-height: 0;

    gap: 12px;

    @include vp-1199 {
      flex-wrap: nowrap;
    }
  }

  a {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 25px;
    height: 25px;

    background-color: $color-default-white;
    border-radius: 50%;
    transition: background-color $trans-default;

    svg {
      fill: $color-black-russian;

      transition: fill $trans-default;
    }

    @include hover-focus {
      background-color: $color-egyptian-blue;

      svg {
        fill: $color-default-white;
      }

      &:active {
        background-color: $color-sapphire;
      }
    }

    &:active {
      background-color: $color-sapphire;

      svg {
        fill: $color-default-white;
      }
    }

    &:disabled {
      background-color: $color-default-white;
      opacity: 0.3;

      svg {
        fill: $color-default-black;
      }
    }
  }
}
